























































































import { Vue, Component } from 'vue-property-decorator'
import { addMajor, changeMajor, deleteMajor, getMajors } from 'utils/api'

@Component({})
export default class SetProfessions extends Vue {
  professions: any = []
  selected: any = []
  listLoading: boolean = false

  editFormVisible: boolean = false // 
  editLoading: boolean = false
  editForm: any = {
    name: '',
    time: '',
    acceptArt: '',
    comment: '',
    college: '',
    stuNum: ''
  }
  editFormWidth: string = '120px'

  addFormVisible: boolean = false // 
  addLoading: boolean = false
  addForm: any = {
    name: '',
    time: '',
    acceptArt: '',
    comment: '',
    college: '',
    stuNum: ''
  }
  addFormRules: any = {
    name: [
      { required: true, message: '', trigger: 'change' }
    ],
    time: [
      { required: true, message: '', trigger: 'change' }
    ],
    comment: [
      { required: true, message: '', trigger: 'change' }
    ],
    college: [
      { required: true, message: '', trigger: 'change' }
    ],
    stuNum: [
      { required: true, message: '', trigger: 'change' }
    ]
  }
  addFormWidth: string = '180px'

  mounted () {
    this.$nextTick(() => {
      this.getProfessions()
    })
  }

  getProfessions () {
    // 
    getMajors()
      .then(res => {
        this.professions = res.data
        this.professions.forEach(major => {
          major.acceptArt = major.acceptArt ? '' : ''
        })
      })
      .catch(err => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  handleEdit (row: any) {
    // 
    this.editFormVisible = true
    this.editForm = (<any> Object).assign({}, row)
  }

  editSubmit () {
    let form = this.editForm
    form.majorid = form.id
    form.acceptArt = form.acceptArt === ''
    changeMajor(form)
      .then(res => {
        if (res.data.succeed) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.editFormVisible = false
          this.getProfessions()
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error'
          })
        }
      })
      .catch(err => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  addSubmit () {
    // 
    let form = this.addForm
    form.acceptArt = form.acceptArt === ''
    addMajor(form)
      .then(res => {
        if (res.data.succeed) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.addFormVisible = false
          this.getProfessions()
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error'
          })
        }
      })
      .catch(err => {
        this.$message({
          message: err.toString(),
          type: 'error'
        })
      })
  }

  handleRemove (row: any) {
    // 
    this.$confirm('')
      .then(() => {
        deleteMajor({
          majorid: row.id
        }).then(res => {
          if (res.data.succeed) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.getProfessions()
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        }).catch(err => {
          this.$message({
            message: err.toString(),
            type: 'error'
          })
        })
      })
      .catch(() => {
      })
  }

  batchRemove () {
    // 
    this.$confirm('')
      .then(() => {
        this.selected.forEach(major => {
          deleteMajor({
            majorid: major.id
          }).then(res => {
            if (res.data.succeed) {
              this.$message({
                message: '',
                type: 'success'
              })
              this.getProfessions()
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              })
            }
          }).catch(err => {
            this.$message({
              message: err.toString(),
              type: 'error'
            })
          })
        })
      }).catch(() => {
    })
  }

  handleCurrentChange (val) {
    this.selected = val
  }

  handleDialogClose () {
    let _this: any = this
    _this.$confirm('')
      .then(() => {
        this.editFormVisible = false
        this.addFormVisible = false
      })
      .catch(() => {
      })
  }
}



